// @ts-nocheck
import "./App.css";
import React, { useEffect, useState } from "react";

//ui
import { Button, TextField, Card } from "@material-ui/core";

//logic
import { DAppClient } from "@airgap/beacon-sdk";
//import _ from "underscore";
//import { FirebaseContext } from "./firebaseContext";
import { Profile } from './components/profile';
import { useNavigate } from "react-router-dom";
import { ProfileList } from "./components/ProfileList";


const dAppClient = new DAppClient({ name: "Beacon Docs" });


function App() {
  const navigate = useNavigate();
  const [activeAccount, setActiveAccount] = useState();
  const [synced, setSynced] = useState("sync");
  const [showUnsync, setShowUnsync] = useState(false);
  const [address, setAddress] = useState();
  const [walletInput, setWalletInput] = useState();
  const [showList, setShowList] = useState(false);

  useEffect(() => {
    async function getAddress() {
      console.log(new URL(window.location.href).searchParams.get('profile'))
      let profileAddress = new URL(window.location.href).searchParams.get('profile');
      let list = new URL(window.location.href).searchParams.get('list');
      //setAddress(profileAddress);
      if (list) {
        setShowList(true);
      }
      else {
        if (!(profileAddress && profileAddress.includes(".tez"))) {
          setAddress(profileAddress);
        }
        else if (profileAddress) {

          await fetch('https://api.tezos.domains/graphql', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              query: `
                {
                  domain(name: "` + profileAddress + `") {
                    name
                    address
                  }
                }
                `,
              variables: {
              },
            }
            ),
          })
            .then((res) => res.json())
            .then((result) => {
              console.log(result);
              if (result.data.domain) {
                setAddress(result.data.domain.address);
              }
            });
        }
      }
    }
    getAddress();
  }, [setAddress]);

  useEffect(() => {
    async function getAcc() {
      setActiveAccount(await dAppClient.getActiveAccount());
      if (activeAccount) {
        setSynced(
          activeAccount.address.slice(0, 6) +
          "..." +
          activeAccount.address.slice(32, 36)
        );
        setShowUnsync(true);
        if (!address && !showList) {
          navigate("/?profile=" + activeAccount.address);
        }
      } else {
        setSynced("sync");
        setShowUnsync(false);
      }
    }
    getAcc();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeAccount]);

  async function unsync() {
    setActiveAccount(await dAppClient.getActiveAccount());
    if (activeAccount) {
      // User already has account connected, everything is ready
      dAppClient.clearActiveAccount().then(async () => {
        setActiveAccount(await dAppClient.getActiveAccount());
        setSynced("sync");
        setShowUnsync(false);
      });
    }
  }

  async function sync() {
    setActiveAccount(await dAppClient.getActiveAccount());
    //Already connected
    if (activeAccount) {
      setSynced(activeAccount.address);
      setShowUnsync(true);
      if (!address) {
        navigate("/?profile=" + activeAccount.address);
      }
      return activeAccount;
    }
    // The user is not synced yet
    else {
      try {
        console.log("Requesting permissions...");
        const permissions = await dAppClient.requestPermissions();
        setActiveAccount(await dAppClient.getActiveAccount());
        console.log("Got permissions:", permissions.address);
        setSynced(permissions.address);
        setShowUnsync(true);
        navigate("/?profile=" + permissions.address);
      } catch (error) {
        console.log("Got error:", error);
      }
    }
  }

  const handleChangeInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    setWalletInput(event.target.value)
  };

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      navigate("/?profile=" + event.target.value);
      setAddress(event.target.value);
    }
  };

  return (
    <div

    >
      <div
        className="top-left"
        style={{
          fontSize: "1em",
          display: "flex",
          alignItems: "center",
          margin: 6,
          fontFamily: "CaviarDreams",
        }}
      >
        <b>Profile Assemblr </b>
      </div>

      {!showUnsync &&
        <div>

          <div style={{ textAlign: "center", margin: 100 }}>
            welcome sync and explore
          </div>
          <div style={{ textAlign: "center", margin: 100 }}>
            profile search module
          </div>
          <div style={{ textAlign: "center", margin: 100 }}>
            display random profile
          </div>
        </div>
      }
      {
        (address || activeAccount) && !showList &&
        <Profile
          key={activeAccount + address}
          address={address ? address : activeAccount.address}
          account={activeAccount}
        />
      }
      {showList && <ProfileList />}
      <div
        className="top-right"
        style={{ position: "absolute", display: "flex", alignItems: "center" }}
      >
        {showUnsync && (
          <Button
            size={"small"}
            title={"unsync"}
            onClick={() => {
              unsync();
            }}
          >
            <u>unsync</u>{" "}
          </Button>
        )}

        {showUnsync && <div> | </div>}
        <Button
          title={"sync"}
          size={"small"}
          onClick={async () => {
            if (!showUnsync) {
              await sync();
            } else {
              navigate("/?profile=" + activeAccount.address);
              setAddress(activeAccount.address);
            }
          }}
        >
          <u>{synced}</u>{" "}
        </Button>
      </div>

      <div
        className="bottom"
        style={{ position: "sticky" }}
      >


        <div
          style={{ display: "flex", alignItems: "center", justifyContent: "right" }}
        >
          <div >
            <Card style={{ display: "flex", alignItems: "center", padding: 10 }}>
              enter wallet address  &nbsp;  &nbsp;
              <TextField id="outlined-basic" label={"tz2FjJh...CRMxdFw"} variant="outlined"
                onChange={handleChangeInput}
                value={walletInput}
                onKeyPress={handleKeyPress}
              />
              <Button
                variant="contained" color="primary"
                style={{ marginLeft: 10 }}
                onClick={() => {
                  navigate("/?profile=" + walletInput);
                  setAddress(walletInput);
                }}
              >
                explore
              </Button>
            </Card>
          </div>
        </div>

        <div
          style={{ position: "sticky", display: "flex", alignItems: "center", justifyContent: "space-between", backgroundColor: "white", width: "100%" }}
        >
          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurenetworks.net/#/", "_blank");
            }}
          >
            <u>Adventure Network</u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://trydrum.net/", "_blank");
            }}
          >
            <u>  TryDrum ! </u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://metaverses.fyi/#/", "_blank");
            }}
          >
            <u>  Metaverses.fyi  </u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://peopleparty.xyz/", "_blank");
            }}
          >
            <u>  People Party  </u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://elementals.run/", "_blank");
            }}
          >
            <u>  Elementals.run  </u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://network1.cc/", "_blank");
            }}
          >
            <u>  Network 1  </u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network2/", "_blank");
            }}
          >
            <u>  Network 2  </u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network3/", "_blank");
            }}
          >
            <u>  Network 3  </u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network4/", "_blank");
            }}
          >
            <u>  Network 4  </u>{" "}
          </Button>

          <Button
            title={"sync"}
            size={"small"}
            onClick={async () => {
              window.open("https://asknetwork.xyz/", "_blank");
            }}
          >
            <u>  Network 5  </u>{" "}
          </Button>
        </div>
      </div>
    </div>
  );
}

export default App;
